body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.App-header {
  padding: 10px;
  background-color: forestgreen;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.shelf {
  margin: 20px;
  width: calc(100% - 20px);
}

.book-list {
  width: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.book {
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
  width: 200px;
}

.thumbnail {
  width: 160px;
  height: 240px;
  background-size: cover;
  position: relative;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.title {
  margin-top: 10px;
}

.author {
  color: darkgray;
}

.shelf-select {
  position: absolute;
  right: -10px;
  bottom: -10px;
  width: 32px;
  height: 32px;
  background-color: forestgreen;
  background-image: url('./icons/caret_down.svg');
  background-size: 100% 100%;
  background-origin: content-box;
  background-repeat: no-repeat;
  padding: 5px;
  border: 0;
  font-size: 0;
  vertical-align: middle;
  cursor: pointer;
  outline: none;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.shelf-select select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

.add-button {
  position: fixed;
  bottom: 40px;
  right: 40px;
  width: 48px;
  height: 48px;
  background-color: forestgreen;
  background-image: url('./icons/add.svg');
  background-size: 100% 100%;
  background-origin: content-box;
  background-repeat: no-repeat;
  padding: 20px;
  border: 0;
  border-radius: 50%;
  font-size: 0;
  vertical-align: middle;
  cursor: pointer;
  outline: none;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

/* width is (100% - margin-left - margin-right) */
.search-form {
  width: calc(100% - 40px);
  display: flex;
  flex-direction: row;
  margin: 20px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}

.search-input {
  width: 100%;
  margin-right: 10px;
  margin-bottom: 10px;
  font-size: inherit;
  background: transparent;
  border: none;
  border-bottom: 1px solid #ccc;
  outline: 0;
}

.back-button {
  width: 60px;
  height: 60px;
  background-image: url('./icons/arrow-back.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 30px;
  font-size: 0;
}
